import Sidebar from "@components/sidebar";
import { isObjEmpty } from "@utils";
import classNames from "classnames";
import { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { FormattedMessage } from "react-intl";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";
import { Button, Form, FormGroup, Input, Label } from "reactstrap";
import validateOptions from "../../../../../constants/validate";
import { add } from "../store/action";
const SidebarAdd = ({ open, toggleSidebar, disable, setDisable }) => {
  const [discountData, setDiscountData] = useState(null);
  const store = useSelector((state) => state.discounts);
  const lang = useSelector((state) => state.common.language);
  const PriceOptions = validateOptions.Document_Link_Options;
  const { id } = useParams();
  const dispatch = useDispatch();
  const { register, errors, handleSubmit } = useForm();
  useEffect(() => {
    if (store?.err?.statusCode) {
      setDisable(false);
    }
  }, [store?.err]);
  const onSubmit = (values) => {
    if (isObjEmpty(errors)) {
      setDisable(true);

      dispatch(
        add({
          product_id: parseInt(id),
          lang,
          ...values,
        })
      );
    }
  };
  return (
    <Sidebar
      size="lg"
      open={open}
      title={<FormattedMessage id="New Version" />}
      headerClassName="mb-1"
      contentClassName="pt-0"
      toggleSidebar={toggleSidebar}
    >
      <Form onSubmit={handleSubmit(onSubmit)}>
        <FormGroup>
          <Label for="link_document_api">
            <FormattedMessage id="Link" />{" "}
          </Label>
          <Input
            name="link_document_api"
            id="link_document_api"
            placeholder=" "
            innerRef={register(PriceOptions.document_link)}
            onBlur={() => {
              let link = document.getElementById("link");
              if (link && link.value) {
                link.value = link.value.trim();
              }
            }}
            className={classNames({
              "is-invalid": errors["link_document_api"],
            })}
          />
          <small className="text-danger">
            {errors?.link_document_api && errors.link_document_api.message}
          </small>
        </FormGroup>
        <div style={{ textAlign: "end" }}>
          <Button
            type="submit"
            className="mr-1"
            color="primary"
            disabled={disable}
          >
            <FormattedMessage id="add" />
          </Button>
          <Button
            type="reset"
            color="secondary"
            outline
            onClick={toggleSidebar}
          >
            <FormattedMessage id="Cancel" />
          </Button>
        </div>
      </Form>
    </Sidebar>
  );
};

export default SidebarAdd;
